.navbar {
  display: flex;
  flex-direction: row;
  gap: 10px;
  height: 65px;
  padding: 20px;
  background: linear-gradient(90.41deg, #023e8a 1.14%, #0096c7 100%);
  color: #FFFFFF;
}

.app-logo {
  width: 45px;
  object-fit: contain;
  align-self: center;
}

.app-title {
  align-self: center;
  font-size: 1.5rem;
  font-weight: 700;
}

.form {
  width: 75%;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.input-area {
  justify-content: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
}

.form-input {
  flex-grow: 1;
  height: 40px;
  border-radius: 5px;
  text-indent: 5px;
  font-size: 1rem;
  border: 1px solid #023e8a;
}

.form-btn {
  width: 50%;
  margin: auto;
  min-height: 40px;
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background: linear-gradient(90.41deg, #023e8a 1.14%, #0096c7 100%);
}

.meme {
  position: relative;
  max-width: 600px;
  margin: auto;
}

.meme-img {
  display: block;
  max-width: 100%;
  margin: 20px auto;
}

.meme-text {
  position: absolute;
  max-width: 80%;
  overflow-wrap: break-word;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}

@media only screen and (max-width: 600px) {
  .form-btn {
    width: 100%;
  }
}